import router from '../router/index'
import store from  '../store/store'
import {stringify} from 'qs'//qsvue--  qswebpack
//import {clearStorage} from 'service/utils.js'
import axios from 'axios'
//192.168.1.8:8091
const debug = process.env.NODE_ENV !== 'production'//,
// axios.defaults.baseURL = M_BASE_URL;
// axios.defaults.method = 'POST';
axios.defaults.timeout = 10000;//ajax10s
axios.defaults.data = {//ajax
  // logintoken: store.state.uuid
};
axios.defaults.withCredentials=true;//session
axios.defaults.transformRequest = [function (data) {
  //qs
  debug; //ajax--debug:,
 // data.CustData = JSON.stringify(data.CustData);//stringifyjson

  data = stringify(data);//stringifyjson

  return data;
}];

// axios.defaults.headers.post['X-Requested-With'] = 'XMLHttpRequest';
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';


/**/
axios.interceptors.request.use(function (config) {
  if(localStorage.token){
    var token=localStorage.token;
    config.headers.Authorization = token;
    config.headers['Content-Type'] = 'application/x-www-form-urlencoded';
      }
  //
  return config;
},
  err => {
    return Promise.reject(err);
  }
  );
// 
axios.interceptors.response.use(function (response) {
  //
  debug;  //ajax
  if(response.data.status == 10002){

    localStorage.clear();
    router.push('/login');
  }
  if(response.data.status == 40003){

    router.push('/');
  }
  return response.data;
},function (error) {
  if (error.response) {
    switch (error.response.status) {
      case 500:

    }
  }
  return Promise.reject(error.response.data)   // 
});






